import React from 'react';
import request from 'superagent';
import _ from 'underscore';

export default class HomeAxes extends React.Component {
  constructor(props) {
    super(props);
  }

  homeAxes(inputAxes) {
    const axes = { x: false, y: false, z: false };
    _.extend(axes, inputAxes);
    let gcode = '';
    if (axes.x && axes.y && axes.z) {
      gcode = 'G28';
    } else if (axes.x && axes.y) {
      gcode = 'G28 X Y';
    } else if (axes.x && axes.z) {
      gcode = 'G28 X Z';
    } else if (axes.y && axes.z) {
      gcode = 'G28 Y Z';
    } else if (axes.x) {
      gcode = 'G28 X';
    } else if (axes.y) {
      gcode = 'G28 Y';
    } else if (axes.z) {
      gcode = 'G28 Z';
    }

    request.post(this.props.endpoint)
    .send({ command: `processGcode` })
    .send({ gcode })
    .set('Accept', 'application/json')
    .end(() => {
      if (axes.z) {
        request.post(this.props.endpoint)
        .send({ command: `processGcode` })
        .send({ gcode: `G1 Z0 F${this.pros.bot.settings.jogZSpeed}` })
        .set('Accept', 'application/json')
        .end();
      }
    });
  }

  render() {
    return (
      <div>
        <h3>Home!</h3>
        <div className="row">
          <div className="col-sm-3">
            <button onClick={() => this.homeAxes({ x: true })}>Home X</button>
          </div>
          <div className="col-sm-3">
          <button onClick={() => this.homeAxes({ y: true })}>Home Y</button>
          </div>
          <div className="col-sm-3">
          <button onClick={() => this.homeAxes({ z: true })}>Home Z</button>
          </div>
          <div className="col-sm-3">
          <button onClick={() => this.homeAxes({ x: true, y: true, z: true })}>Home All</button>
          </div>
        </div>
      </div>
    );
  }
}
